body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar{
    background-image: linear-gradient(to bottom,black 5%,transparent 95%);
    border-bottom: none;
}
#navbar{
    height:60px;
    color:white;
    text-align: left;
    font-size: 20px;
    display: flex;
    width: 100%;
    position: fixed;
    top:0;
  padding-top:10px;
  z-index:6;
  flex-wrap: wrap;
}
.solid-navbar{
    background-image: linear-gradient(to bottom,black 95%,white 100%);
  
  -webkit-animation-name: solidNavbar;
  
          animation-name: solidNavbar;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.Logo{
flex:20% 1;
}
.searchBar{
   padding:4px 0px 0px 50px;
   flex:40% 1;
  display:flex;
}
.main-buttons-container{
    flex:40% 1;
}
.icon-common{
    color:black;
    padding:10px 10px;
}
#login{
   
    text-decoration: none;
} 
.search{
   
   height:30px;
   color:black;
}
.search:hover{
    border:1px solid blue;
}
.btn-search{
 
}


.navbar:hover{
  -webkit-animation-name: navbarAnimate;
          animation-name: navbarAnimate;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes navbarAnimate{
    0%{
        height:60px;
    }
    100%{
        height:80px;
    }
}
@keyframes navbarAnimate{
    0%{
        height:60px;
    }
    100%{
        height:80px;
    }
}
@-webkit-keyframes solidNavbar{
    0%{
        -webkit-transform: translateY(-80px);
                transform: translateY(-80px);
    }
    100%{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}
@keyframes solidNavbar{
    0%{
        -webkit-transform: translateY(-80px);
                transform: translateY(-80px);
    }
    100%{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}
@media screen and (max-width:748px){
    .solid-navbar{
        display:none;
    }
    #navbar{
        display:none; 
    }
    .navbar{
        display:none;
    }
}
