.footer-container{
    width:100%;
    height:40px;
    /* background: linear-gradient(to bottom, black 5%,transparent 95%); */
    position:fixed;
    bottom:0;
    z-index:10;
    display:flex;
    flex-grow: 1;
    background-color: black;
}
.icon-fawesome{
    flex:20% 1;
   padding-top:10px;
   cursor: pointer;
}
.searchBar-footer-container{
    display:flex;
    width:100%;
    height:40px;
    position: fixed;
    bottom:0;
    z-index:6;
    padding-left:10px;
    -webkit-animation-name: footer;
            animation-name: footer;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
#search-input-footer{
flex:80% 1;
border:none;
background-color: white;
color:black;
}
#search-input-footer:hover{
   border:1px solid blue
}
#btn-footer{
    padding:2px;
    margin-left:5px;
    border:none;
    text-decoration: none;
    color:white;
    flex:10% 1;
}
.close-btn{
    flex:10% 1;

    height:40px;
    color:white;
    width:20px;
    text-align: center;
    cursor:pointer;
}
@-webkit-keyframes footer{
    0%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    100%{
        -webkit-transform:translateX(0%);
                transform:translateX(0%);
    }
}
@keyframes footer{
    0%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    100%{
        -webkit-transform:translateX(0%);
                transform:translateX(0%);
    }
}
